    /*++++++++++++++++++*/
    // 
    broadcastTransaction(signedTransaction, options = {}, callback = false) {
        if (!callback)
            return this.injectPromise(this.broadcastTransaction, signedTransaction, options);

        this.aschWeb.fullNode.request(urls.postApi.url, {
            transaction: signedTransaction
        }, 'post', {
            headers: {
                magic: this.isMainnet ? urls.magics.production : urls.magics.development,
                version: ''
            }
        }).then(res => {
            if (!Object.keys(res).length)
                return callback('response not found');
            callback(null, res);
        }).catch(err => callback(err));
    }


    //

    async buildAndSign(transaction = false, mnemnic = this.aschWeb.defaultMnemonic, secondSecret = false, callback = false) {
        console.log('mnemnic:', mnemnic);
        if (!callback)
            return this.injectPromise(this.buildAndSign, transaction, mnemnic, secondSecret);
        transaction.secret = mnemnic;
        transaction.secondSecret = secondSecret ? secondSecret : null;
        transaction = aschFee.convertFee(transaction);
        transaction = AschJS.transaction.createTransactionEx(transaction);
        //console.log('transaction:',transaction);
        if (transaction) {
            return callback(null, transaction);
        } else {
            return callback('buildAndSign failed.');
        }
    }

    async broadcastTransactionEx(type, args, message, options = {}, callback = false) {
        if (!callback)
            return this.injectPromise(this.broadcastTransactionEx, type, args, message, options = {});
        try {
            const transaction = {
                type: type, //
                args: args, //
                message: message //
            };
            const signedTransaction = await this.buildAndSign(transaction);
            const result = await this.broadcastTransaction(signedTransaction);
            console.log('result:', result);
            return callback(null, result);
        } catch (ex) {
            return callback(ex);
        }
    }


