    // 
    async createProposal(title, desc, topic, content, endHeight, options = {}, callback = false) {
        try {
            const transaction = {
                type: 300,
                args: [title, desc, topic, content, endHeight]
            };
            const signedTransaction = await this.buildAndSign(transaction);
            const result = await this.broadcastTransaction(signedTransaction);
            return callback(null, result);
        } catch (ex) {
            return callback(ex);
        }
    }
